<template>
  <div class="container">
    <div class="input-group mb-3 mt-3">
      <input type="text" class="form-control" v-model="inputUsername" placeholder="請輸入使用者名稱。" aria-label="Recipient's username" aria-describedby="basic-addon2">
      <div class="input-group-append">
        <button class="btn btn-primary" @click="submitUsername()" type="button">傳送</button>
      </div>
    </div>
    <div class="input-group mb-3 mt-3">
      <input type="text" class="form-control" v-model="inputMessage" placeholder="請輸入訊息。" aria-label="Recipient's username" aria-describedby="basic-addon2">
      <div class="input-group-append">
        <button class="btn btn-primary" @click="submitMessage()" type="button">傳送</button>
      </div>
    </div>
    <div class="media my-5" v-for="obj in messages">
      <img v-if="obj.username !== username" class="mr-3" src="https://dummyimage.com/100x100/000/00ffd5.png" alt="Generic placeholder image">
      <div class="media-body">
        <h5 class="mt-0">{{obj.username}}</h5>
        <p>{{obj.message}}</p>
        </div>
      <img v-if="obj.username === username" class="ml-3" src="https://dummyimage.com/100x100/000/00ffd5.png" alt="Generic placeholder image">
    </div>
  </div>
</template>

<script>
	export default {
		name: "hello2",
    data() {
		  return {
		    msg: 'HELLO!!!!',
        username: 'D',
        inputMessage: '',
        inputUsername: '',
        messages: [
          {
            username: 'test1',
            message: 'hello!!!!'
          },
          {
            username: 'D',
            message: 'hello123!!!'
          }
        ]
      }
    },
    methods: {
		  submitMessage: function () {
		    this.messages.push({
          username: this.username,
          message: this.inputMessage
        })
      },
      submitUsername: function () {
        this.username = this.inputUsername
      }
    }
	}
</script>

<style scoped>

</style>



